<template>
  <div class="descripcion-content">
    <div class="container">
      <div class="card card-muni">
        <div class="top-title">
          <div>
            <h5>{{nombreTipoTramite}}</h5>
            <p class="area">{{nombreArea | upper}}</p>
          </div>
          <div class="mobile-hide">
            <a href="javascript:;" @click="IniciarTramite()" class="btn btn-default">Iniciar Trámite</a>
          </div>
        </div>
        <P>{{descripcionTipoTramite}}</P>
        <ul>
          <li>
            <p class="principal">
              <b>Costo</b>
            </p>
            <span>{{precioTramite==0 ? 'El trámite es gratuito' : 'El trámite tiene un costo de S/.' + precioTramite}}</span>
          </li>
          <li>
            <p class="principal">
              <b>{{id014Tramite==33 ? 'Información TUSNE' : id014Tramite==34 ? 'Información TUPA' : 'Requisitos'}}</b>
            </p>
            <span>
              <div v-if="(codigoTipoTramite!=null && codigoTipoTramite!=''  )" v-html="tupaTusneDescripcion"></div>
              <div v-else>
                <ol>
                  <li
                    v-for="requisito of listaRequisitos"
                    :key="requisito.idRequisitoTramite"
                  >{{requisito.requisito.nombre}}</li>
                </ol>
              </div>
            </span>
          </li>
          <li>
            <p class="principal">
              <b>Base Legal</b>
            </p>
            <span>
              <div v-html="baseLegal"></div>
            </span>
          </li>
          <li>
            <p class="principal">
              <b>Tener en Cuenta</b>
            </p>
            <span>
              <div v-html="tenerEnCuenta"></div>
            </span>
          </li>
          <li>
            <p class="principal">
              <b>Preguntas Frecuentes</b>
            </p>
            <span>
              <div v-html="preguntasFrecuentes"></div>
            </span>
          </li>
        </ul>
        <div class="mobile-block">
          <a href="javascript:;" @click="IniciarTramite()" class="btn btn-default">Iniciar Trámite</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import EventBus from "../../helpers/EventBus.js"
import Constantes from "../../store/constantes.js";
import _ from "lodash"
export default {
  created() {
    this.idArea=this.$route.params.idArea;
    this.tipoDoc = this.$route.query.new;
    this.cargarTipoTramite();
    this.cargarUnidadOrganica();
    this.cargarRequisitos();
  },
  data() {
    return {
      tipoDoc: null,
      nombreTipoTramite: "",
      codigoTipoTramite: "",
      idTipoTramite: 0,
      idTipoTramiteIngreso: 0,
      id009TipoTramite: 0,
      flagOnLine: false,
      idArea: 0,
      nombreArea: "",
      descripcionTipoTramite: "",
      listaRequisitos: null,
      listaCanalAtencion: null,
      listaPreguntas: null,
      secuenciaSamd: 0,
      letraSamd: "",
      idSubConcepto: 0,
      precioTramite: 0,
      nombreUsuario: "",
      tupaTusneDescripcion: "",
      baseLegal: "",
      tenerEnCuenta: "",
      preguntasFrecuentes: "",
      id014Tramite: 0
    };
  },
  filters: {
     upper: function (value) {
      return value.toLowerCase();
    }
  },
  methods: {
    cargarPrecio() {
      this.precioTramite = 0;
      if (this.codigoTipoTramite != "") {
        axios
          .get(
            Constantes.rutaTramite +
              "tipotramite/precio/" +
              this.codigoTipoTramite +
              "/" +
              this.idSubConcepto
          )
          .then(response => {
            console.log("precio:" + response.data.precio);
            this.precioTramite = response.data.precio;
          })
          .catch(e => console.log(e));
      }
    },
    cargarRequisitos() {
      EventBus.$emit("loadershow", true);
      axios
        //.get(Constantes.rutaTramite +"requisito-tramite/" +this.$route.params.idTipoTramite + "/" +this.idArea)
        .get(Constantes.rutaTramite +"requisito-tramite/" +this.$route.params.idTipoTramite + "/" +0)
        .then(response => {
          console.log("Cargando requisitos:");
          console.log(response.data.data);
          this.listaRequisitos = response.data.data;
          EventBus.$emit("loadershow", false);
        })
        .catch(e => console.log(e));
    },
    IniciarTramite() {
      if(this.listaRequisitos==null || this.listaRequisitos.length==0){
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Aún no se han definido requisitos para este trámite"
        });
      }else{
        if (this.flagOnLine) {
          let endpoint = this.tipoDoc ? "/tramite/ce/" + this.idArea + "&" + this.$route.params.idTipoTramite + "&0&0" + "?new=3" : "/tramite/ce/" + this.idArea + "&" + this.$route.params.idTipoTramite + "&0&0"
          this.$router.push(endpoint);
        } else {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Trámite no disponible de manera online."
          });
        }
      }
      
    },
    cargarUnidadOrganica() {
      var rutaprod = "https://mz-services.miraflores.gob.pe:8090/api/tramites/";
      axios
        //.get(rutaprod + "unidadorganica/" + this.$route.params.idArea)
        .get(rutaprod + "area/" + this.$route.params.idArea+"/0")
        .then(response => {
          console.log(response.data.data);
          //this.nombreArea = response.data.data[0].uniorgnom;
          this.nombreArea = response.data[0].nombreArea;
          console.log("area Actual: ", this.nombreArea);
          //console.log(this.listaTipoTramite[0].nombre);
        })
        .catch(e => console.log(e));
    },
    cargarTipoTramite() {
      var rutaprod = "http://localhost:8090/api/tramites/";
      axios
        .get(
          Constantes.rutaTramite +
            "tipotramite/inicializar/" +
            this.$route.params.idTipoTramite
        )
        .then(response => {
          this.id009TipoTramite =
            response.data.data.id009TipoMetodo.idParametro;
          this.codigoTipoTramite = response.data.data.codigo;
          console.log("pintando codigo:" + this.codigoTipoTramite);
          this.nombreTipoTramite = response.data.data.nombre;
          this.flagOnLine = response.data.data.flagOnLine;
          this.idTipoTramiteIngreso =
            response.data.data.id008EquivalenciaOracle;
          this.idTipoTramite = this.$route.params.idTipoTramite;
          this.descripcionTipoTramite = response.data.data.descripcion;
          this.idSubConcepto = response.data.data.idSubConcepto;
          this.tupaTusneDescripcion = response.data.data.tupaTusneDes;
          this.baseLegal = response.data.data.baseLegal;
          this.tenerEnCuenta = response.data.data.tenerEnCuenta;
          this.preguntasFrecuentes = response.data.data.preguntasFrecuentes;
          this.id014Tramite = response.data.data.id014Tramite;
          console.log(response.data.data);
          this.cargarPrecio();
          //console.log(this.listaTipoTramite[0].nombre);
        })
        .catch(e => console.log(e));
    }
  }
};
</script>
<style lang="scss" scoped>
.descripcion-content {
  height: 100%;
  position: relative;
  margin-bottom: 100px;
  .mobile-block {
    display: none;
  }
  .top-title {
    display: flex;
    align-content: space-between;
    div:first-child {
      width: 82%;
    }
    div:last-child {
      width: 18%;
      text-align: right;
    }
  }
  ul {
    margin-left: 0px;
    padding-left: 0px;
    list-style: none;
    border: solid 1px #eaeaea;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 70px;
    li {
      background: #eaf0f9;
      border-bottom: solid 1px #eaeaea;
      > p.principal {
        color: #636363;
        background: #fff;
        margin-bottom: 0px;
        padding: 10px;
      }
      > span {
        padding: 10px;
        display: block;
      }
    }
  }
  h5 {
    font-size: 15px;
    color: #0078cf;
    font-weight: normal;
  }
  .area {
    color: #2db5c3;
    font-weight: 500;
    text-transform: capitalize;
  }
}
@media only screen and (max-width: 600px) {
.descripcion-content {
    ul {
      margin-bottom: 0px;
    }
    .mobile-block {
      padding-top: 40px;
      padding-bottom: 60px;
      text-align: center;
      display: block;
    }
    .top-title {
      flex-direction: column-reverse;
      > div {
        width: 100% !important;
      }
      h5 {
        width: 100%;
      }
    }
    .mobile-hide {
      display: none;
    }
  }
}
</style>


